.limiter {
    width: 1366px;
    margin: 0 auto;
}

.container-table100 {
    width: 100%;
    min-height: 100vh;
    background: #fff;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 33px 30px;
}

.wrap-table100 {
    width: 1170px;
}

.table100.ver1 {
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0px 40px 0px rgb(0 0 0 / 15%);
    -moz-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
    -webkit-box-shadow: 0 0px 40px 0px rgb(0 0 0 / 15%);
    -o-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
    -ms-box-shadow: 0 0px 40px 0px rgba(0, 0, 0, 0.15);
}
.table100 {
    position: relative;
    padding-top: 60px;
}
.table100 {
    background-color: #fff;
}
.m-b-110 {
    margin-bottom: 110px;
}

.table100-head {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
}

table {
    width: 100%;
}
table {
    border-collapse: collapse;
}

.table100.ver1 th {
    font-size: 18px;
    color: #fff;
    line-height: 1.4;
    background-color: #6c7ae0;
}
.table100-head th {
    padding-top: 18px;
    padding-bottom: 18px;
}
.column1 {
    width: 10%;
    padding-left: 40px;
}
.column2 {
    width: 10%;
    padding-left: 10px;
}
.column3 {
    width: 35%;
    padding-left: 10px;
}
.column4 {
    width: 35%;
    padding-left: 40px;
}
.column5 {
    width: 10%;
    padding-left: 10px;
}
th, td {
    font-weight: unset;
    padding-right: 10px;
}
th {
    text-align: left;
}

.table100.ver1 td {
    font-family: Lato-Regular;
    font-size: 15px;
    color: #808080;
    line-height: 1.4;
}
.table100-body td {
    padding-top: 16px;
    padding-bottom: 16px;
}
th, td {
    font-weight: unset;
    padding-right: 10px;
}

.table100.ver1 .table100-body tr:nth-child(even) {
    background-color: #f8f6ff;
}

.common-input {
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
